@font-face {
  font-family: 'Speedy Font';
  src:
    url('../fonts/SpeedyRegular-7BLoE-min.woff2');
}

@font-face {
  font-family: 'Nerd Font';
  src:
    url('../fonts/nerdFontCustom/icomoon.woff2');
}

#calculator {
  position: absolute;
  outline: 1px solid red;
  outline-offset: -1px;
  aspect-ratio: 3 / 4; 
  max-width: fit-content;
  height: 80vh;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-flow: column nowrap;
  user-select: none;
  -webkit-user-select: none;

  & *,
  & .speedy-font-container {
    font-family: 'Speedy Font', sans-serif;

    &.nerd-font-container * {
      font-family: 'Nerd Font', sans-serif;
    }
  }

  & > #visor {
    display: flex;
    justify-content: space-between;
    flex: 0.3;
    align-items: center;
    position: relative;
    font-size: 3rem;
    padding: 0 2rem;

    & > #operator-display {
      flex: 1;
    }

    & > #number-display {
      flex: 5;
      position: relative;
      overflow-x: scroll;
      scrollbar-width: none;
      display: flex;
      flex-direction: row-reverse;
      justify-content: flex-start;
      user-select: text;

      & > p {
        width: max-content;
        margin: 0;
      }
    }

    & > #clipboard {
      position: absolute;
      top: 0;
      right: 0;
      font-size: 2rem;
    }
  }

  & > #buttons {
    flex: 1;
    display: grid;
    grid-template: repeat(5, 1fr) / repeat(4, 1fr);
    margin-top: .5rem;

    & button:is(.number, .operator, .special) {
      border: 1px solid darkgray;
      font-size: 1.7rem;
      transition: background-color 0.08s ease-out;
    }

    & button.number {
      background-color: white;

      &:hover { background-color: #d0e8ff }
      &:active { background-color: #b0c8ff }
    }

    & button.operator {
      background-color: black;
      color: white;

      &:hover { background-color: #708090 }
      &:active { background-color: #506090 }
    }

    & button.special {
      background-color: #D43775;
      color: white;

      &:hover { background-color: #C427AA }
      &:active { background-color: #A4078A }
    }

    & #positions {
      display: grid;
      grid-template: repeat(2, 1fr) / repeat(2, 1fr);
    }

    & > #main-pad {
      display: grid;
      grid-area: 1 /  1 /span 4 / span 3;
      grid-template: repeat(4, 1fr) / repeat(3, 1fr);

      & > button.special {
        order: 1;
      }
    }

    & > #side-pad {
      display: flex;
      grid-area: 1 /  -2 /-1 / -1;
      flex-flow: column nowrap;

      & > * {
        flex: 1;
      }
    }

    & > #bottom-pad {
      display: grid;
      grid-area: -2 /  1 /-1 / span 3;
      grid-template-columns: repeat(3, 1fr);

      & > * {
        flex: 1;
      }
    }
  }
}
