@font-face {
  font-family: Speedy Font;
  src: url("SpeedyRegular-7BLoE-min.95be4d39.woff2");
}

@font-face {
  font-family: Nerd Font;
  src: url("icomoon.9c9dfbb2.woff2");
}

#calculator {
  outline-offset: -1px;
  aspect-ratio: 3 / 4;
  user-select: none;
  outline: 1px solid red;
  flex-flow: column;
  max-width: fit-content;
  height: 80vh;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  & *, & .speedy-font-container {
    font-family: Speedy Font, sans-serif;

    &.nerd-font-container * {
      font-family: Nerd Font, sans-serif;
    }
  }

  & > #visor {
    flex: .3;
    justify-content: space-between;
    align-items: center;
    padding: 0 2rem;
    font-size: 3rem;
    display: flex;
    position: relative;

    & > #operator-display {
      flex: 1;
    }

    & > #number-display {
      scrollbar-width: none;
      user-select: text;
      flex-direction: row-reverse;
      flex: 5;
      justify-content: flex-start;
      display: flex;
      position: relative;
      overflow-x: scroll;

      & > p {
        width: max-content;
        margin: 0;
      }
    }

    & > #clipboard {
      font-size: 2rem;
      position: absolute;
      top: 0;
      right: 0;
    }
  }

  & > #buttons {
    flex: 1;
    grid-template: repeat(5, 1fr) / repeat(4, 1fr);
    margin-top: .5rem;
    display: grid;

    & button:is(.number, .operator, .special) {
      border: 1px solid #a9a9a9;
      font-size: 1.7rem;
      transition: background-color 80ms ease-out;
    }

    & button.number {
      background-color: #fff;

      &:hover {
        background-color: #d0e8ff;
      }

      &:active {
        background-color: #b0c8ff;
      }
    }

    & button.operator {
      color: #fff;
      background-color: #000;

      &:hover {
        background-color: #708090;
      }

      &:active {
        background-color: #506090;
      }
    }

    & button.special {
      color: #fff;
      background-color: #d43775;

      &:hover {
        background-color: #c427aa;
      }

      &:active {
        background-color: #a4078a;
      }
    }

    & #positions {
      grid-template: repeat(2, 1fr) / repeat(2, 1fr);
      display: grid;
    }

    & > #main-pad {
      grid-template: repeat(4, 1fr) / repeat(3, 1fr);
      grid-area: 1 / 1 / span 4 / span 3;
      display: grid;

      & > button.special {
        order: 1;
      }
    }

    & > #side-pad {
      flex-flow: column;
      grid-area: 1 / -2 / -1 / -1;
      display: flex;

      & > * {
        flex: 1;
      }
    }

    & > #bottom-pad {
      grid-area: -2 / 1 / -1 / span 3;
      grid-template-columns: repeat(3, 1fr);
      display: grid;

      & > * {
        flex: 1;
      }
    }
  }
}
